  margin-right: 5px;
}
.sidebar{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 75px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    background: #545454;
    color: #f7fff7;
    height: 100%;
    transition: width 0.5s;
}
.sidebar.change{
    width: 200px;
}

.SideList{
    padding-left: 15px;
    padding-right: 15px;
    list-style: none;
    font-size: 24px;
    transition: 0.5s;
    opacity: 0;
}
.change .SideList{
    opacity: 1;
}
.SideList li{
    border-bottom: 1px #f7fff7 solid;
    padding-left: 25px;
    cursor: pointer;
}
.SideList li:hover{
    background: #f7fff7;
    color: #545454;
}

.container {
    -webkit-align-self: flex-end;
            align-self: flex-end;
    cursor: pointer;
    margin-right: 20px;
    margin-left: 20px;
    margin-top: 20px;
}
  
.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: #f7fff7;
    margin: 6px 0;
    transition: 0.4s;
}

.change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {opacity: 0;}

.change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
}
.Tile{
    width: 450px;
